import React from 'react';

import s from "../AboutPage/AboutPage.module.scss";

const ContactsPage = () => {
    return (
        <section className={s.Contacts}>
            <div className="container">
                <div className={s.Contacts__content}>
                    <h2>Contacts Page</h2>
                </div>
            </div>
        </section>
    );

};

export default ContactsPage;
